import React, { useState } from "react";
import {
  Search,
  Upload,
  RefreshCw,
  ArrowRight,
  AlertTriangle,
  Clock,
  BarChart3,
  FileText,
  Settings,
  Sparkles,
} from "lucide-react";

export default function PolishedDocCompare() {
  const [docName, setDocName] = useState("Contract Agreement");
  const [v1Label, setV1Label] = useState("Draft v1.0");
  const [v2Label, setV2Label] = useState("Final v2.0");
  const [fileV1, setFileV1] = useState(null);
  const [fileV2, setFileV2] = useState(null);
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState(null);
  const [filterType, setFilterType] = useState("ALL");

  const demoResult = {
    doc_name: "Contract Agreement",
    v1_label: "Draft v1.0",
    v2_label: "Final v2.0",
    pages_v1: 12,
    pages_v2: 14,
    paragraphs_v1: 87,
    paragraphs_v2: 92,
    changes_count: 24,
    risk_level: "MEDIUM",
    summary_text: "การเปรียบเทียบพบความแตกต่างที่สำคัญในหมวด 3.2 เกี่ยวกับเงื่อนไขการชำระเงิน มีการเพิ่มข้อกำหนดใหม่ 5 ข้อ และแก้ไขวลีที่สำคัญใน Section 4 เกี่ยวกับความรับผิดชอบ\n\nข้อควรระวัง: พบการเปลี่ยนแปลงในวันที่มีผลบังคับใช้จากเดิม 30 มิ.ย. เป็น 15 ก.ค. 2025 ซึ่งอาจมีผลต่อไทม์ไลน์โปรเจกต์",
    changes: [
      {
        change_type: "MODIFIED",
        section_label: "Section 3.2 - Payment Terms",
        old_text: "Payment shall be made within 30 days of invoice date",
        new_text: "Payment shall be made within 45 days of invoice date, with early payment discount of 2% if paid within 15 days",
        risk_level: "HIGH",
        ai_comment: "การเปลี่ยนแปลงเงื่อนไขการชำระเงินอาจส่งผลต่อ cash flow"
      },
      {
        change_type: "ADDED",
        section_label: "Section 5.1 - Liability Cap",
        old_text: null,
        new_text: "Total liability under this agreement shall not exceed the total contract value",
        risk_level: "MEDIUM",
        ai_comment: "เพิ่มข้อจำกัดความรับผิดชอบ ควรทบทวนให้รอบคอบ"
      },
      {
        change_type: "REMOVED",
        section_label: "Appendix B - Pricing Schedule",
        old_text: "Hourly rate for senior consultants: $250/hour",
        new_text: null,
        risk_level: "LOW",
        ai_comment: "ลบรายละเอียดราคา อาจเป็นการย้ายไปเอกสารแนบอื่น"
      }
    ]
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading(true);
    setTimeout(() => {
      setResult(demoResult);
      setLoading(false);
    }, 2000);
  };

  const riskColor = (risk) => {
    const level = risk?.toUpperCase() || "LOW";
    if (level.includes("HIGH")) {
      return "bg-gradient-to-br from-red-50 to-red-100 text-red-700 border-red-200 shadow-sm shadow-red-100";
    }
    if (level.includes("MEDIUM")) {
      return "bg-gradient-to-br from-amber-50 to-amber-100 text-amber-700 border-amber-200 shadow-sm shadow-amber-100";
    }
    return "bg-gradient-to-br from-emerald-50 to-emerald-100 text-emerald-700 border-emerald-200 shadow-sm shadow-emerald-100";
  };

  const renderChangeBadge = (type) => {
    const base = "inline-flex items-center gap-1.5 px-2.5 py-1 rounded-lg text-[11px] font-bold uppercase tracking-wide";
    if (type === "ADDED")
      return <span className={`${base} bg-emerald-500 text-white shadow-sm`}>+ Added</span>;
    if (type === "REMOVED")
      return <span className={`${base} bg-rose-500 text-white shadow-sm`}>− Removed</span>;
    return <span className={`${base} bg-amber-500 text-white shadow-sm`}>~ Modified</span>;
  };

  const filteredChanges = result?.changes.filter(c => 
    filterType === "ALL" ? true : c.change_type === filterType
  ) || [];

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-white to-violet-50/30 p-4 md:p-8">
      <div className="max-w-7xl mx-auto">
        {/* Header with animated gradient */}
        <div className="relative mb-8 overflow-hidden rounded-3xl bg-gradient-to-r from-violet-600 via-fuchsia-500 to-pink-500 p-[2px] shadow-2xl shadow-violet-500/20">
          <div className="relative rounded-3xl bg-white p-6 md:p-8">
            <div className="absolute inset-0 bg-gradient-to-br from-violet-50/50 via-transparent to-pink-50/50 rounded-3xl" />
            
            <div className="relative flex items-start justify-between gap-4">
              <div className="flex items-start gap-4">
                <div className="relative">
                  <div className="absolute inset-0 bg-gradient-to-br from-violet-500 to-fuchsia-500 rounded-2xl blur-xl opacity-50 animate-pulse" />
                  <div className="relative flex h-16 w-16 items-center justify-center rounded-2xl bg-gradient-to-br from-violet-600 via-fuchsia-500 to-pink-500 shadow-lg">
                    <Search className="h-8 w-8 text-white" />
                  </div>
                </div>
                
                <div>
                  <div className="inline-flex items-center gap-2 rounded-full bg-gradient-to-r from-violet-100 to-fuchsia-100 px-3 py-1 mb-2">
                    <Sparkles className="h-3.5 w-3.5 text-violet-600" />
                    <span className="text-xs font-bold text-violet-700 uppercase tracking-wide">AI-Powered Analysis</span>
                  </div>
                  <h1 className="text-3xl md:text-4xl font-extrabold text-slate-900 mb-2 bg-gradient-to-r from-slate-900 to-slate-700 bg-clip-text">
                    Document Version Comparison
                  </h1>
                  <p className="text-slate-600 text-sm md:text-base max-w-2xl">
                    Upload two PDF versions to detect changes, analyze risks, and get AI-powered summaries instantly
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Main content grid */}
        <div className="grid lg:grid-cols-[1.4fr,1fr] gap-6 mb-6">
          {/* Left: Upload section */}
          <div className="space-y-6">
            <div className="relative overflow-hidden rounded-2xl bg-white border border-slate-200 shadow-lg shadow-slate-200/50">
              <div className="absolute inset-0 bg-gradient-to-br from-violet-50/30 via-transparent to-fuchsia-50/30" />
              
              <div className="relative p-6 space-y-6">
                {/* Document Info */}
                <div className="space-y-4">
                  <div className="flex items-center gap-2">
                    <div className="h-8 w-8 rounded-lg bg-gradient-to-br from-violet-500 to-fuchsia-500 flex items-center justify-center">
                      <FileText className="h-4 w-4 text-white" />
                    </div>
                    <h3 className="text-sm font-bold text-slate-900 uppercase tracking-wide">Document Information</h3>
                  </div>
                  
                  <div className="grid gap-4 md:grid-cols-3">
                    <div className="space-y-2">
                      <label className="text-xs font-semibold text-slate-700">Document Name</label>
                      <input
                        type="text"
                        value={docName}
                        onChange={(e) => setDocName(e.target.value)}
                        className="w-full rounded-xl border-2 border-slate-200 bg-white px-4 py-2.5 text-sm font-medium text-slate-900 transition-all focus:border-violet-400 focus:ring-4 focus:ring-violet-100 focus:outline-none"
                        placeholder="e.g., Contract Agreement"
                      />
                    </div>
                    <div className="space-y-2">
                      <label className="text-xs font-semibold text-slate-700">Version 1 Label</label>
                      <input
                        type="text"
                        value={v1Label}
                        onChange={(e) => setV1Label(e.target.value)}
                        className="w-full rounded-xl border-2 border-slate-200 bg-white px-4 py-2.5 text-sm font-medium text-slate-900 transition-all focus:border-violet-400 focus:ring-4 focus:ring-violet-100 focus:outline-none"
                        placeholder="e.g., Draft v1.0"
                      />
                    </div>
                    <div className="space-y-2">
                      <label className="text-xs font-semibold text-slate-700">Version 2 Label</label>
                      <input
                        type="text"
                        value={v2Label}
                        onChange={(e) => setV2Label(e.target.value)}
                        className="w-full rounded-xl border-2 border-slate-200 bg-white px-4 py-2.5 text-sm font-medium text-slate-900 transition-all focus:border-violet-400 focus:ring-4 focus:ring-violet-100 focus:outline-none"
                        placeholder="e.g., Final v2.0"
                      />
                    </div>
                  </div>
                </div>

                {/* File Upload Cards */}
                <div className="grid md:grid-cols-2 gap-4">
                  {/* Version 1 */}
                  <div className="group relative overflow-hidden rounded-2xl border-2 border-dashed border-violet-200 bg-gradient-to-br from-violet-50 to-violet-100/50 p-5 transition-all hover:border-violet-400 hover:shadow-lg hover:shadow-violet-200/50">
                    <div className="absolute top-3 right-3">
                      <div className="flex h-7 w-7 items-center justify-center rounded-full bg-white shadow-sm border border-violet-200">
                        <span className="text-xs font-bold text-violet-600">V1</span>
                      </div>
                    </div>
                    
                    <div className="mb-4">
                      <div className="flex items-center gap-2 mb-2">
                        <Upload className="h-5 w-5 text-violet-600" />
                        <p className="text-sm font-bold text-violet-900">Version 1 (PDF)</p>
                      </div>
                      <p className="text-xs text-violet-700">
                        Upload the older or draft version
                      </p>
                    </div>
                    
                    <input
                      type="file"
                      accept="application/pdf"
                      onChange={(e) => setFileV1(e.target.files?.[0])}
                      className="w-full text-xs file:mr-3 file:rounded-full file:border-0 file:bg-gradient-to-r file:from-violet-600 file:to-violet-500 file:px-4 file:py-2 file:text-xs file:font-bold file:text-white file:shadow-md hover:file:shadow-lg file:transition-all cursor-pointer"
                    />
                    
                    {fileV1 && (
                      <div className="mt-3 flex items-center gap-2 rounded-lg bg-white/80 px-3 py-2 border border-violet-200">
                        <FileText className="h-4 w-4 text-violet-600 flex-shrink-0" />
                        <p className="text-xs font-medium text-violet-900 truncate">{fileV1.name}</p>
                      </div>
                    )}
                  </div>

                  {/* Version 2 */}
                  <div className="group relative overflow-hidden rounded-2xl border-2 border-dashed border-fuchsia-200 bg-gradient-to-br from-fuchsia-50 to-pink-100/50 p-5 transition-all hover:border-fuchsia-400 hover:shadow-lg hover:shadow-fuchsia-200/50">
                    <div className="absolute top-3 right-3">
                      <div className="flex h-7 w-7 items-center justify-center rounded-full bg-white shadow-sm border border-fuchsia-200">
                        <span className="text-xs font-bold text-fuchsia-600">V2</span>
                      </div>
                    </div>
                    
                    <div className="mb-4">
                      <div className="flex items-center gap-2 mb-2">
                        <Upload className="h-5 w-5 text-fuchsia-600" />
                        <p className="text-sm font-bold text-fuchsia-900">Version 2 (PDF)</p>
                      </div>
                      <p className="text-xs text-fuchsia-700">
                        Upload the newer or final version
                      </p>
                    </div>
                    
                    <input
                      type="file"
                      accept="application/pdf"
                      onChange={(e) => setFileV2(e.target.files?.[0])}
                      className="w-full text-xs file:mr-3 file:rounded-full file:border-0 file:bg-gradient-to-r file:from-fuchsia-600 file:to-pink-500 file:px-4 file:py-2 file:text-xs file:font-bold file:text-white file:shadow-md hover:file:shadow-lg file:transition-all cursor-pointer"
                    />
                    
                    {fileV2 && (
                      <div className="mt-3 flex items-center gap-2 rounded-lg bg-white/80 px-3 py-2 border border-fuchsia-200">
                        <FileText className="h-4 w-4 text-fuchsia-600 flex-shrink-0" />
                        <p className="text-xs font-medium text-fuchsia-900 truncate">{fileV2.name}</p>
                      </div>
                    )}
                  </div>
                </div>

                {/* Submit Button */}
                <button
                  onClick={handleSubmit}
                  disabled={loading}
                  className="group relative w-full overflow-hidden rounded-xl bg-gradient-to-r from-violet-600 via-fuchsia-500 to-pink-500 p-[2px] shadow-xl shadow-violet-500/25 transition-all hover:shadow-2xl hover:shadow-violet-500/40 disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  <div className="relative flex items-center justify-center gap-2 rounded-[10px] bg-gradient-to-r from-violet-600 via-fuchsia-500 to-pink-500 px-6 py-3.5 transition-all">
                    {loading ? (
                      <>
                        <RefreshCw className="h-5 w-5 text-white animate-spin" />
                        <span className="text-sm font-bold text-white">Analyzing Documents...</span>
                      </>
                    ) : (
                      <>
                        <span className="text-sm font-bold text-white">Compare Documents</span>
                        <ArrowRight className="h-5 w-5 text-white transition-transform group-hover:translate-x-1" />
                      </>
                    )}
                  </div>
                </button>
              </div>
            </div>
          </div>

          {/* Right: Results Preview */}
          <div className="space-y-4">
            {result ? (
              <div className="rounded-2xl bg-white border border-slate-200 shadow-lg shadow-slate-200/50 overflow-hidden">
                <div className="bg-gradient-to-r from-slate-900 to-slate-800 px-6 py-4">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-3">
                      <BarChart3 className="h-5 w-5 text-violet-400" />
                      <h3 className="text-sm font-bold text-white">Comparison Results</h3>
                    </div>
                    <span className={`inline-flex items-center gap-2 rounded-full border-2 px-3 py-1 text-xs font-bold ${riskColor(result.risk_level)}`}>
                      {result.risk_level} RISK
                    </span>
                  </div>
                </div>

                <div className="p-6 space-y-6">
                  {/* Stats Grid */}
                  <div className="grid grid-cols-2 gap-3">
                    <div className="rounded-xl bg-gradient-to-br from-violet-50 to-violet-100 p-4 border border-violet-200">
                      <p className="text-xs font-semibold text-violet-700 mb-1">Pages</p>
                      <p className="text-2xl font-bold text-violet-900">{result.pages_v1} → {result.pages_v2}</p>
                    </div>
                    <div className="rounded-xl bg-gradient-to-br from-fuchsia-50 to-pink-100 p-4 border border-fuchsia-200">
                      <p className="text-xs font-semibold text-fuchsia-700 mb-1">Paragraphs</p>
                      <p className="text-2xl font-bold text-fuchsia-900">{result.paragraphs_v1} → {result.paragraphs_v2}</p>
                    </div>
                    <div className="col-span-2 rounded-xl bg-gradient-to-br from-emerald-50 to-emerald-100 p-4 border border-emerald-200">
                      <p className="text-xs font-semibold text-emerald-700 mb-1">Total Changes Detected</p>
                      <p className="text-3xl font-extrabold text-emerald-900">{result.changes_count}</p>
                    </div>
                  </div>

                  {/* AI Summary */}
                  <div className="space-y-2">
                    <div className="flex items-center gap-2">
                      <Sparkles className="h-4 w-4 text-violet-500" />
                      <p className="text-xs font-bold text-slate-900 uppercase tracking-wide">AI Summary</p>
                    </div>
                    <div className="rounded-xl bg-slate-50 border border-slate-200 p-4 max-h-48 overflow-auto">
                      <p className="text-xs leading-relaxed text-slate-700 whitespace-pre-wrap">
                        {result.summary_text}
                      </p>
                    </div>
                  </div>

                  {/* Action Buttons */}
                  <div className="flex flex-wrap gap-2">
                    <button className="flex-1 inline-flex items-center justify-center gap-2 rounded-lg bg-slate-900 px-4 py-2.5 text-xs font-bold text-white hover:bg-slate-800 transition-colors">
                      <FileText className="h-4 w-4" />
                      View Full Report
                    </button>
                    <button className="inline-flex items-center justify-center gap-2 rounded-lg border-2 border-slate-200 bg-white px-4 py-2.5 text-xs font-bold text-slate-700 hover:bg-slate-50 transition-colors">
                      <Clock className="h-4 w-4" />
                      History
                    </button>
                  </div>
                </div>
              </div>
            ) : (
              <div className="rounded-2xl border-2 border-dashed border-slate-200 bg-gradient-to-br from-slate-50 to-violet-50/30 p-12 text-center">
                <div className="inline-flex h-20 w-20 items-center justify-center rounded-2xl bg-white shadow-lg border border-slate-200 mb-4">
                  <Search className="h-10 w-10 text-slate-400" />
                </div>
                <h3 className="text-sm font-bold text-slate-900 mb-2">No Results Yet</h3>
                <p className="text-xs text-slate-600 max-w-xs mx-auto">
                  Upload two PDF versions and click compare to see detailed analysis and AI-powered insights
                </p>
              </div>
            )}
          </div>
        </div>

        {/* Changes Summary Section */}
        {result && result.changes && (
          <div className="rounded-2xl bg-white border border-slate-200 shadow-lg shadow-slate-200/50 overflow-hidden">
            <div className="bg-gradient-to-r from-slate-900 to-slate-800 px-6 py-4">
              <div className="flex items-center justify-between flex-wrap gap-3">
                <div className="flex items-center gap-3">
                  <div className="flex h-8 w-8 items-center justify-center rounded-lg bg-violet-500">
                    <Settings className="h-5 w-5 text-white" />
                  </div>
                  <h3 className="text-sm font-bold text-white">Detailed Changes ({filteredChanges.length})</h3>
                </div>

                <div className="flex gap-2">
                  {["ALL", "ADDED", "REMOVED", "MODIFIED"].map(type => (
                    <button
                      key={type}
                      onClick={() => setFilterType(type)}
                      className={`px-3 py-1.5 rounded-lg text-xs font-bold transition-all ${
                        filterType === type
                          ? "bg-white text-slate-900 shadow-md"
                          : "bg-slate-800 text-slate-300 hover:bg-slate-700"
                      }`}
                    >
                      {type}
                    </button>
                  ))}
                </div>
              </div>
            </div>

            <div className="p-6 max-h-[600px] overflow-auto space-y-4">
              {filteredChanges.map((change, idx) => (
                <div key={idx} className="group rounded-xl border border-slate-200 bg-slate-50 p-5 hover:shadow-md hover:border-slate-300 transition-all">
                  <div className="flex items-start justify-between gap-3 mb-4">
                    <div className="flex items-center gap-3">
                      {renderChangeBadge(change.change_type)}
                      <span className="text-xs font-semibold text-slate-600">
                        {change.section_label || "General Section"}
                      </span>
                    </div>
                    <span className={`inline-flex items-center gap-1 rounded-lg border-2 px-2.5 py-0.5 text-xs font-bold ${riskColor(change.risk_level)}`}>
                      {change.risk_level || "LOW"}
                    </span>
                  </div>

                  <div className="grid md:grid-cols-2 gap-4 mb-3">
                    {change.old_text && (
                      <div className="space-y-1.5">
                        <p className="text-xs font-bold text-slate-700 uppercase tracking-wide">Previous</p>
                        <div className="rounded-lg bg-rose-50 border border-rose-200 p-3">
                          <p className="text-xs text-rose-900 line-through leading-relaxed">
                            {change.old_text}
                          </p>
                        </div>
                      </div>
                    )}
                    {change.new_text && (
                      <div className="space-y-1.5">
                        <p className="text-xs font-bold text-slate-700 uppercase tracking-wide">Current</p>
                        <div className="rounded-lg bg-emerald-50 border border-emerald-200 p-3">
                          <p className="text-xs text-emerald-900 leading-relaxed">
                            {change.new_text}
                          </p>
                        </div>
                      </div>
                    )}
                  </div>

                  {change.ai_comment && (
                    <div className="rounded-lg bg-violet-50 border border-violet-200 p-3">
                      <div className="flex items-start gap-2">
                        <Sparkles className="h-4 w-4 text-violet-600 flex-shrink-0 mt-0.5" />
                        <div>
                          <p className="text-xs font-bold text-violet-900 mb-1">AI Analysis</p>
                          <p className="text-xs text-violet-700 leading-relaxed">{change.ai_comment}</p>
                        </div>
                      </div>
                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}